import {carConfig} from "../../../api/mall.js"
export default {
	data() {
		return {
			carId:'',
			info:[]
		}
	},
	onLoad(opts) {
		this.carId = opts.id ||''
		this.getCarConfig()
	},
	methods: {
		getCarConfig(){
			uni.showLoading({
				title:'加载中...',
				mask:true
			})
			carConfig({
				data:{
					id:this.carId
				} 
			}).then(res=>{
				uni.hideLoading()
				if(res.data.code == 0) {
					this.info = res.data.result.infos
				}
			}).catch(()=>{
				uni.hideLoading()
			})
		}
	}
}
